html, body, .app {
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Courier New', 'Arial';
  color: #FF6B6B;
}

.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.content__container {
  position: relative;
  height: 100%;
  background: -webkit-linear-gradient(60deg, #4ECDC4, #FF6F6D 99%);
  background: -o-linear-gradient(60deg, #4ECDC4, #FF6F6D 99%);
  background: linear-gradient(30deg, #4ECDC4, #FF6F6D 99%);
  display: -ms-flexbox;
  display: flex;
}



